<template>
  <div class="account">
    <van-nav-bar
      title="账号管理"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />

    <van-cell-group>
      <van-cell class="avatar" title="头像" size="large">
        <template #right-icon>
          <div class="person-img">
            <img class="auto-img" :src="userInfo.userImg" alt="" />
            <van-uploader :after-read="upload" class="v-uploader" />
          </div>
        </template>
      </van-cell>

      <van-cell title="唯一ID" size="large" :value="userInfo.userId" />
      <van-cell title="手机号" size="large" :value="userInfo.phone" />

      <van-cell title="昵称" size="large" class="avatar">
        <template #right-icon>
          <div>
            <van-field
              class="v-field"
              input-align="right"
              v-model="userInfo.nickName"
              @change="
                changeUserInfo('/updateNickName', 'nickName', userInfo.nickName)
              "
            />
          </div>
        </template>
      </van-cell>

      <van-cell title="个人简介" size="large">
        <template #right-icon>
          <div>
            <van-field
              class="v-textarea"
              input-align="right"
              rows="2"
              type="textarea"
              autosize
              show-word-limit
              maxlength="30"
              placeholder="今天也是随幸的一天"
              @change="changeUserInfo('/updateDesc', 'desc', userInfo.desc)"
              v-model="userInfo.desc"
            />
          </div>
        </template>
      </van-cell>
    </van-cell-group>
  </div>
</template>

<script>
import { utils } from "../utils/utils";
export default {
  name: "Account",
  data() {
    return {
      userInfo: {
        nickname: "",
        desc: "",
        userImg: "",
        phone: "",
        userId: "",
      },
    };
  },

  created() {
    if (!this.isLogin) {
      this.$router.push({ name: "Login" });
      return;
    }
    this.getUserInfo();
  },
  computed: {
    isLogin() {
      return this.$store.state.isLogin;
    },
  },

  methods: {
    back() {
      this.$router.go(-1);
    },

    //获取个人信息
    getUserInfo() {
      let tokenString = localStorage.getItem("_t");

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });

      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((d) => {
          this.$toast.clear();

          this.userInfo = d.data.result[0];
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //修改个人信息
    changeUserInfo(url, key, value) {
      let tokenString = localStorage.getItem("_t");
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });

      this.axios({
        method: "POST",
        url,
        data: {
          appkey: this.appkey,
          tokenString,
          [key]: value,
        },
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          this.$toast.clear();
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //上传头像
    upload(file) {
      //控制图片大小不超过500kb
      let imgSize = 500 * 1024;
      if (file.file.size > imgSize) {
        this.$toast("上传图片大小不能超过500KB");
        return;
      }

      //将图片上传到服务器
      //获取图片类型
      let imgType = file.file.type.split("/")[1];
      //处理图片的base64
      let serverBase64Img = file.content.replace(
        /data:image\/[A-Za-z]+;base64,/,
        ""
      );

      let tokenString = localStorage.getItem("_t");

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "正在加载...",
      });

      this.axios({
        method: "POST",
        url: "/updateAvatar",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img,
        },
        transformRequest: utils.transformRequest,
      })
        .then((d) => {
          this.$toast.clear();
          //
          this.userInfo.userImg = d.data.userImg;
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
.account {
  padding-top: 46px;
  .avatar {
    padding-top: 0;
    padding-bottom: 0;
    line-height: 48px;
    .person-img {
      width: 40px;
      margin-top: 4px;
      position: relative;
    }
    .v-uploader {
      position: absolute;
      width: 40px;
      height: 40px;
      left: 0;
      top: 0;
      overflow: hidden;
      z-index: 2;
      opacity: 0;
    }
  }
  /deep/ .v-field {
    padding: 0;
    line-height: 48px;
  }
  /deep/ .v-textarea {
    padding: 0;
  }
  /deep/ .van-field__control {
    color: #9697a6;
  }
}
</style>